/*Расширение экрана*/
body {
  padding: 0;
  margin: 0;
  background-color: #1B2D37; }

.header-logo__image {
  display: block;
  position: relative;
  margin-top: 34px;
  margin-left: 14px; }

.header-logo {
  display: flex; }

.header-logo div {
  margin-top: 39px;
  margin-left: 14px; }

.header-logo__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  margin: 0;
  color: #FFFFFF; }

.header-logo__subtitle {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin: 0;
  color: #788891; }

@media (max-width: 1023px) {
  .header-logo__image {
    margin-top: 20px;
    margin-left: 6px; }
  .header-logo div {
    margin-top: 27px;
    margin-left: 14px; } }

@media (max-width: 767px) {
  .header-logo__image {
    width: 38px;
    height: 38px;
    margin-top: 16px;
    margin-left: -3px; }
  .header-logo__title,
  .header-logo__subtitle {
    display: none; } }

.main-navigation {
  display: flex;
  align-items: center;
  padding-top: 34px; }

@media (max-width: 1023px) {
  .main-navigation {
    padding-top: 24px; } }

.contacts-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  margin-right: 50px; }

.contacts-list__link {
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 18px;
  text-decoration: none;
  color: #ffffff; }

.contacts-list__item {
  margin-right: 120px;
  position: relative; }
  .contacts-list__item:last-child {
    margin-right: 0; }

.contacts-list__link-icon--email {
  position: absolute;
  width: 18px;
  height: 14px;
  top: 1px;
  left: -38px; }

.contacts-list__link-icon--phone {
  position: absolute;
  width: 18px;
  height: 18px;
  top: -2px;
  left: -32px; }

@media (max-width: 1023px) {
  .contacts-list__item {
    margin-right: 0;
    position: relative; }
  .contacts-list__link span {
    display: none; }
  .contacts-list__link-icon--email {
    width: 35px;
    height: 18px;
    top: -13px;
    left: -41px; }
  .contacts-list__link-icon--phone {
    position: absolute;
    width: 32px;
    height: 24px;
    top: -16px;
    left: 18px; } }

@media (max-width: 767px) {
  .contacts-list__link-icon--email {
    width: 35px;
    height: 18px;
    top: -14px;
    left: -23px; }
  .contacts-list__link-icon--phone {
    position: absolute;
    width: 32px;
    height: 24px;
    top: -17px;
    left: 28px; } }

.callback-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 45px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px;
  outline: none;
  color: #ffffff;
  cursor: pointer; }
  .callback-button:hover, .callback-button:focus {
    background-color: #ffffff;
    border-color: #00b2ff;
    color: #00b2ff; }

@media (max-width: 1023px) {
  .callback-button {
    display: none; } }

.wrapper {
  max-width: 1423px;
  margin: 0 auto;
  padding: 0 20px;
  outline: 1px solid red;
  display: flex;
  justify-content: space-between; }
